<template>
  <div class="settings-index-page">
    <h1>WPM 设置</h1>

    <MyIndexList>
      <MyIndexListItem
        v-for="item in items1"
        :key="item.href"
        :icon="item.icon"
        :label="item.label"
        :value="count(item)"
        :href="item.href"
      />
    </MyIndexList>

    <h3 v-show="debug">for debug:</h3>
    <MyIndexList v-show="debug">
      <MyIndexListItem
        v-for="item in items2"
        :key="item.href"
        :icon="item.icon"
        :label="item.label"
        :value="count(item)"
        :href="item.href"
      />
    </MyIndexList>
  </div>
</template>
  
<script>
import MyIndexList from "./index/index-list.vue";
import MyIndexListItem from "./index/index-list-item.vue";

const items1 = [
  {
    icon: "/image/icon_local_repositories.png",
    label: "本地仓库",
    href: "/settings/repositories",
    value: 666,
    name: "repositories",
  },
  {
    icon: "/image/icon_projects.png",
    label: "工程项目",
    href: "/settings/projects",
    value: 666,
    name: "projects",
  },
  {
    icon: "/image/icon_content_types.png",
    label: "内容类型",
    href: "/settings/content-types",
    value: 666,
    name: "content_types",
  },
  {
    icon: "/image/icon_executables.png",
    label: "应用程序",
    href: "/settings/executables",
    value: 666,
    name: "executables",
  },
  {
    icon: "/image/icon_media.png",
    label: "媒体文件",
    href: "/settings/media",
    value: 666,
    name: "mediae",
  },
  {
    icon: "/image/icon_intent_templates.png",
    label: "命令模板",
    href: "/settings/intent-templates",
    value: 666,
    name: "intent_templates",
  },
  {
    icon: "/image/icon_plug_ins.png",
    label: "插件",
    href: "/settings/plug-ins",
    value: 666,
    name: "plug_ins",
  },
  {
    icon: "/image/icon_backups.png",
    label: "数据导入/导出",
    href: "/settings/backups",
    value: 666,
    name: "xxx-backups",
  },
];

const items2 = [
  {
    icon: "/image/icon_media.png",
    label: "JSON Buffer",
    href: "/settings/json-buffer",
    value: 666,
    name: "xxxx",
  },
  {
    icon: "/image/icon_media.png",
    label: "开发者模式",
    href: "/settings/dev",
    value: 666,
    name: "xxxx",
  },
];

export default {
  name: "settings-index",

  components: {
    MyIndexList,
    MyIndexListItem,
  },

  computed: {
    debug() {
      return this.$store.getters["options/debug"];
    },
  },

  data() {
    return {
      items1,
      items2,
    };
  },

  methods: {
    count(item) {
      let field = item.name;
      let value = this.$store.getters["statistic/" + field];
      if (value == null) {
        return "-";
      }
      return value;
    },

    fetch() {
      this.$store.dispatch("statistic/fetch");
    },
  },

  mounted() {
    this.fetch();
  },
};
</script>

<style scoped>
.settings-index-page {
  text-align: left;
}
</style>
